<template>
  <div class="app-container">
    <header>
      <div class="header-left">
        <button class="back-btn" @click="goBack">
          <i class="fas fa-arrow-left"></i>
          <span>返回</span>
        </button>
        <div class="logo">
          <i class="fas fa-users-cog"></i>
          <h1>用户管理</h1>
        </div>
      </div>
      <div class="user-info">
        <i class="fas fa-user-circle"></i>
        <span>管理员</span>
      </div>
    </header>

    <div class="main-content">
      <div class="content">
        <div class="toolbar">
          <div class="search-box">
            <input type="text" class="search-input" placeholder="搜索姓名" v-model="searchParams.name">
            <select class="search-input" v-model="searchParams.gender">
              <option value="">全部性别</option>
              <option value="1">男</option>
              <option value="2">女</option>
            </select>
            <input type="text" class="search-input" placeholder="手机号" v-model="searchParams.phone">
          </div>
          <div class="button-group">
            <button class="btn btn-primary" @click="handleSearch">
              <i class="fas fa-search"></i> 搜索
            </button>
            <button class="btn btn-secondary" @click="handleShowAllUsers">
              <i class="fas fa-list"></i> 显示全部
            </button>
            <button class="btn btn-success" @click="openAddModal">
          <i class="fas fa-plus"></i> 添加用户
        </button>
            <button class="btn btn-danger" @click="openBulkDelete">
              <i class="fas fa-trash-alt"></i> 批量删除
            </button>
          </div>
        </div>

        <!-- 桌面端表格视图 -->
        <div class="table-container desktop-table">
          <table class="user-table">
          <thead>
            <tr>
              <th width="50"><input type="checkbox" v-model="selectAll"></th>
              <th>ID</th>
              <th>姓名</th>
              <th>性别</th>
              <th>手机号</th>
              <th>身份证</th>
              <th>角色</th>
              <th>登录密码</th>
              <th>操作</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="user in displayedUsers" :key="user.id">
              <td><input type="checkbox" v-model="selectedUsers" :value="user.id"></td>
              <td>{{ user.id }}</td>
              <td>{{ user.name }}</td>
              <td>
                <span v-if="user.gender === 1" class="gender-male">
                  <i class="fas fa-male"></i> 男
                </span>
                <span v-else class="gender-female">
                  <i class="fas fa-female"></i> 女
                </span>
              </td>
              <td>{{ user.phone }}</td>
              <td>{{ user.idCard }}</td>
              <td>
                <span v-if="user.identity === 1">老人</span>
                <span v-else-if="user.identity === 2">家属</span>
                <span v-else>管理员</span>
              </td>
              <td>{{ user.password }}</td>
              <td class="action-cell">
                <button class="action-btn edit-btn" @click="openEditModal(user)">
                  <i class="fas fa-edit"></i> 编辑
                </button>
                <button class="action-btn delete-btn" @click="deleteUser(user.id)">
                  <i class="fas fa-trash-alt"></i> 删除
                </button>
                <button 
                  v-if="user.identity !== 3" 
                  class="action-btn bind-btn" 
                  @click="queryBindInfo(user)"
                >
                  <i class="fas fa-link"></i> 绑定信息
                </button>
              </td>
            </tr>
            <tr v-if="displayedUsers.length === 0">
              <td colspan="9" style="text-align: center; padding: 20px;">没有找到匹配的用户数据。请尝试搜索或添加新用户。</td>
            </tr>
          </tbody>
          </table>
        </div>

        <!-- 移动端卡片视图 -->
        <div class="mobile-cards">
          <div v-for="user in displayedUsers" :key="user.id" class="user-card">
            <div class="card-header">
              <div class="card-checkbox">
                <input type="checkbox" v-model="selectedUsers" :value="user.id">
              </div>
              <div class="card-title">
                <h3>{{ user.name }}</h3>
                <span class="user-id">ID: {{ user.id }}</span>
              </div>
              <div class="card-gender">
                <span v-if="user.gender === 1" class="gender-male">
                  <i class="fas fa-male"></i> 男
                </span>
                <span v-else class="gender-female">
                  <i class="fas fa-female"></i> 女
                </span>
              </div>
            </div>
            <div class="card-body">
              <div class="card-info">
                <div class="info-item">
                  <span class="label">手机号:</span>
                  <span class="value">{{ user.phone }}</span>
                </div>
                <div class="info-item">
                  <span class="label">身份证:</span>
                  <span class="value">{{ user.idCard }}</span>
                </div>
                <div class="info-item">
                  <span class="label">角色:</span>
                  <span class="value">
                    <span v-if="user.identity === 1">老人</span>
                    <span v-else-if="user.identity === 2">家属</span>
                    <span v-else>管理员</span>
                  </span>
                </div>
                <div class="info-item">
                  <span class="label">密码:</span>
                  <span class="value">{{ user.password }}</span>
                </div>
              </div>
              <div class="card-actions">
                <button class="action-btn edit-btn" @click="openEditModal(user)">
                  <i class="fas fa-edit"></i> 编辑
                </button>
                <button class="action-btn delete-btn" @click="deleteUser(user.id)">
                  <i class="fas fa-trash-alt"></i> 删除
                </button>
                <button 
                  v-if="user.identity !== 3" 
                  class="action-btn bind-btn" 
                  @click="queryBindInfo(user)"
                >
                  <i class="fas fa-link"></i> 绑定信息
                </button>
              </div>
            </div>
          </div>
          <div v-if="displayedUsers.length === 0" class="no-data-card">
            <p>没有找到匹配的用户数据。请尝试搜索或添加新用户。</p>
          </div>
        </div>

        <div class="pagination" v-if="totalPages > 1">
          <button class="page-btn" :disabled="currentPage === 1" @click="currentPage--">
            <i class="fas fa-chevron-left"></i> 上一页
          </button>
          <button
            v-for="page in totalPages"
            :key="page"
            class="page-btn"
            :class="{ active: currentPage === page }"
            @click="currentPage = page"
          >
            {{ page }}
          </button>
          <button class="page-btn" :disabled="currentPage === totalPages" @click="currentPage++">
            下一页 <i class="fas fa-chevron-right"></i>
          </button>
        </div>
      </div>
    </div>

    <div class="footer-info">
      © 2025 用户管理系统 | 当前版本: 3.2.0 | 数据更新时间: {{ currentDate }}
    </div>

    <div class="modal-overlay" v-if="showAddModal">
      <div class="user-modal">
        <div class="modal-header">
          <h2>添加新用户</h2>
          <button class="close-btn" @click="showAddModal = false">
            <i class="fas fa-times"></i>
          </button>
        </div>
        <div class="modal-body">
          <div class="form-group">
            <label>姓名</label>
            <input type="text" class="form-control" v-model="newUser.name">
          </div>
          <div class="form-group">
            <label>性别</label>
            <div class="radio-group">
              <label class="radio-option">
                <input type="radio" v-model="newUser.gender" value="1"> 男
              </label>
              <label class="radio-option">
                <input type="radio" v-model="newUser.gender" value="2"> 女
              </label>
            </div>
          </div>
          <div class="form-group">
            <label>身份证号</label>
            <input type="text" class="form-control" v-model="newUser.idCard">
          </div>
          <div class="form-group">
            <label>手机号</label>
            <input type="text" class="form-control" v-model="newUser.phone">
          </div>
          <div class="form-group">
            <label>地址</label>
            <input type="text" class="form-control" v-model="newUser.address">
          </div>
          <div class="form-group">
            <label>角色</label>
            <select class="form-control" v-model="newUser.identity">
              <option value="1">老人</option>
              <option value="2">家属</option>
              <option value="3">管理员</option>
            </select>
          </div>
          <div class="form-group">
            <label>密码</label>
            <input type="password" class="form-control" v-model="newUser.password">
          </div>
        </div>
        <div class="modal-footer">
          <button class="btn" @click="showAddModal = false">取消</button>
          <button class="btn btn-success" @click="addUser">添加</button>
        </div>
      </div>
    </div>


    
    <div class="modal-overlay" v-if="showEditModal">
      <div class="user-modal">
        <div class="modal-header">
          <h2>编辑用户信息</h2>
          <button class="close-btn" @click="showEditModal = false">
            <i class="fas fa-times"></i>
          </button>
        </div>
        <div class="modal-body">
          <div class="form-group">
            <label>姓名</label>
            <input type="text" class="form-control" v-model="editingUser.name">
          </div>
          <div class="form-group">
            <label>性别</label>
            <div class="radio-group">
              <label class="radio-option">
                <input type="radio" v-model="editingUser.gender" value="1"> 男
              </label>
              <label class="radio-option">
                <input type="radio" v-model="editingUser.gender" value="2"> 女
              </label>
            </div>
          </div>
          <div class="form-group">
            <label>身份证号</label>
            <input type="text" class="form-control" v-model="editingUser.idCard">
          </div>
          <div class="form-group">
            <label>手机号</label>
            <input type="text" class="form-control" v-model="editingUser.phone">
          </div>
          <div class="form-group">
            <label>紧急联系人</label>
            <input type="text" class="form-control" v-model="editingUser.emergency_phone">
          </div>
          <div class="form-group">
            <label>地址</label>
            <input type="text" class="form-control" v-model="editingUser.address">
          </div>
          <div class="form-group">
            <label>角色</label>
            <select class="form-control" v-model="editingUser.identity">
              <option value="1">老人</option>
              <option value="2">家属</option>
              <option value="3">管理员</option>
            </select>
          </div>
          <div class="form-group">
            <label>密码</label>
            <input type="password" class="form-control" v-model="editingUser.password" placeholder="留空则不修改">
          </div>
        </div>
        <div class="modal-footer">
          <button class="btn" @click="showEditModal = false">取消</button>
          <button class="btn btn-success" @click="updateUser">保存</button>
        </div>
      </div>
    </div>


    
    <!-- 绑定信息弹窗 -->
    <div class="modal-overlay" v-if="showBindInfoModal">
      <div class="user-modal bind-info-modal">
        <div class="modal-header">
          <h2>
            <i class="fas fa-link"></i>
            {{ bindInfo.userName }} 的{{ bindInfo.queryType }}信息
          </h2>
          <button class="close-btn" @click="showBindInfoModal = false">
            <i class="fas fa-times"></i>
          </button>
        </div>
        <div class="modal-body">
          <div v-if="bindInfo.data && bindInfo.data.length > 0" class="bind-info-list">
            <div v-for="(item, index) in bindInfo.data" :key="index" class="bind-info-item">
              <div class="bind-info-header">
                <span class="bind-info-number">{{ index + 1 }}</span>
                <h4>{{ item.name || '未知' }}</h4>
              </div>
              <div class="bind-info-details">
                <div class="info-row">
                  <i class="fas fa-phone"></i>
                  <span class="label">手机号：</span>
                  <span class="value">{{ item.phone || '未知' }}</span>
                </div>
                <div class="info-row">
                  <i class="fas fa-users"></i>
                  <span class="label">关系：</span>
                  <span class="value">{{ item.relation || '未知' }}</span>
                </div>
              </div>
            </div>
          </div>
          <div v-else class="no-bind-info">
            <i class="fas fa-info-circle"></i>
            <p>{{ bindInfo.userName }} 暂无{{ bindInfo.queryType }}信息</p>
          </div>
        </div>
        <div class="modal-footer">
          <button class="btn btn-primary" @click="showBindInfoModal = false">
            <i class="fas fa-check"></i> 确定
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, reactive, computed, onMounted, watch } from 'vue';
import { useRouter } from 'vue-router';
import config from '@/utils/config';


export default {
  setup() {
    const router = useRouter();
    
    const currentDate = ref(new Date().toLocaleDateString('zh-CN', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit'
    }));

    // 用户数据状态
    const allUsers = ref([]); // 存储所有用户数据（用于列表模式）
    const displayedUsers = ref([]); // 当前显示的用户数据
    const totalUsers = ref(0);
    const selectedUsers = ref([]);
    const selectAll = computed({
      get: () => displayedUsers.value.length > 0 && selectedUsers.value.length === displayedUsers.value.length,
      set: (value) => {
        selectedUsers.value = value ? displayedUsers.value.map(user => user.id) : [];
      }
    });

    // 分页状态
    const currentPage = ref(1);
    const pageSize = ref(10);
    const totalPages = computed(() => {
      const pages = Math.ceil(totalUsers.value / pageSize.value);
      return pages > 0 ? pages : 1; // 确保至少显示1页
    });

    // 搜索参数
    const searchParams = reactive({
      name: '',
      gender: '',
      phone: ''
    });

    const showingAllUsers = ref(true);

    // 模态框状态
    const showAddModal = ref(false);
    const showEditModal = ref(false);
    const showBindInfoModal = ref(false);

    // 绑定信息状态
    const bindInfo = reactive({
      userName: '',
      queryType: '',
      data: []
    });

    // 新用户数据
    const newUser = reactive({
      name: '',
      gender: '1',
      idCard: '',
      phone: '',
      address: '',
      identity: '1',
      password: ''
    });

    const editingUser = ref(null);

    // 使用全局配置的API地址
    const API_BASE = config.API_BASE_URL;

    // 获取用户列表
    const fetchUsers = async () => {
      try {
        let apiUrl = '';
        const params = new URLSearchParams();
        
        // 分页参数
        params.append('page', currentPage.value);
        params.append('pageSize', pageSize.value);

        // 搜索条件
        if (!showingAllUsers.value) {
          if (searchParams.name) params.append('name', searchParams.name);
          if (searchParams.gender) params.append('gender', searchParams.gender);
          if (searchParams.phone) params.append('phone', searchParams.phone);
        }

        // 根据模式选择API端点
        apiUrl = showingAllUsers.value ? 
          `${API_BASE}/users/list` : 
          `${API_BASE}/users`;

        const response = await fetch(`${apiUrl}?${params.toString()}`);
        const data = await response.json();

        if (data.code === 1) {
          // 确保data.data存在
          if (!data.data) {
            console.error('API返回数据结构异常，缺少data字段');
            displayedUsers.value = [];
            allUsers.value = [];
            totalUsers.value = 0;
            return;
          }
          
          if (showingAllUsers.value) {
            // 列表模式 - 存储所有用户数据
            allUsers.value = data.data || [];
            totalUsers.value = allUsers.value.length;
            
            // 计算当前页显示的数据
            const start = (currentPage.value - 1) * pageSize.value;
            const end = start + pageSize.value;
            displayedUsers.value = allUsers.value.slice(start, end);
          } else {
            // 搜索模式 - 处理API返回的数据
            // 检查数据结构
            if (data.data && data.data.rows && Array.isArray(data.data.rows)) {
              // 如果data.data包含rows数组和total字段
              displayedUsers.value = data.data.rows || [];
              totalUsers.value = data.data.total || 0;
            } else if (data.data && data.data.records) {
              // 如果data.data包含records属性
              displayedUsers.value = data.data.records || [];
              totalUsers.value = data.data.total || 0;
            } else if (Array.isArray(data.data)) {
              // 如果data.data直接是数组（没有分页信息）
              displayedUsers.value = data.data || [];
              totalUsers.value = data.data.length || 0;
            } else {
              // 默认情况
              displayedUsers.value = [];
              totalUsers.value = 0;
            }
          }
        } else {
          console.error('获取用户列表失败:', data.msg);
          alert(`获取用户列表失败: ${data.msg}`);
          displayedUsers.value = [];
          allUsers.value = [];
          totalUsers.value = 0;
        }
      } catch (error) {
        console.error('获取用户列表失败:', error);
        alert('获取用户列表失败，请检查网络连接');
        displayedUsers.value = [];
        allUsers.value = [];
        totalUsers.value = 0;
      }
    };

    // 处理页码变化时的数据更新（仅用于列表模式）
    const updateDisplayedUsers = () => {
      if (showingAllUsers.value) {
        const start = (currentPage.value - 1) * pageSize.value;
        const end = start + pageSize.value;
        displayedUsers.value = allUsers.value.slice(start, end);
      }
    };

    // 处理搜索
    const handleSearch = () => {
      currentPage.value = 1;
      showingAllUsers.value = false;
      fetchUsers();
    };

    // 显示所有用户
    const handleShowAllUsers = () => {
      searchParams.name = '';
      searchParams.gender = '';
      searchParams.phone = '';
      currentPage.value = 1;
      showingAllUsers.value = true;
      fetchUsers();
    };

    // 添加用户
    const addUser = async () => {
      try {
        const response = await fetch(`${API_BASE}/users`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(newUser)
        });

        const data = await response.json();

        if (data.code === 1) {
          showAddModal.value = false;
          if (showingAllUsers.value) {
            handleShowAllUsers();
          } else {
            handleSearch();
          }
          alert('用户添加成功');
        } else {
          alert(`添加失败: ${data.msg}`);
        }
      } catch (error) {
        console.error('添加用户失败:', error);
        alert('添加用户失败，请检查网络连接');
      }
    };

    // 更新用户
    const updateUser = async () => {
      try {
        const updateData = { id: editingUser.value.id };
        const fields = ['name', 'gender', 'idCard', 'phone', 'emergency_phone', 'address', 'identity'];

        fields.forEach(field => {
          if (editingUser.value[field] !== undefined) {
            updateData[field] = editingUser.value[field];
          }
        });

        if (editingUser.value.password) {
          updateData.password = editingUser.value.password;
        }

        const response = await fetch(`${API_BASE}/users`, {
          method: 'PUT',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(updateData)
        });

        const data = await response.json();

        if (data.code === 1) {
          showEditModal.value = false;
          if (showingAllUsers.value) {
            handleShowAllUsers();
          } else {
            handleSearch();
          }
          alert('用户信息更新成功');
        } else {
          alert(`更新失败: ${data.msg}`);
        }
      } catch (error) {
        console.error('更新用户失败:', error);
        alert('更新用户失败，请检查网络连接');
      }
    };

    // 删除用户
    const deleteUser = async (userId) => {
      if (!confirm('确定要删除这个用户吗？')) return;

      try {
        const response = await fetch(`${API_BASE}/users?ids=${userId}`, {
          method: 'DELETE'
        });

        const data = await response.json();

        if (data.code === 1) {
          if (showingAllUsers.value) {
            handleShowAllUsers();
          } else {
            handleSearch();
          }
          alert('用户删除成功');
        } else {
          alert(`删除失败: ${data.msg}`);
        }
      } catch (error) {
        console.error('删除用户失败:', error);
        alert('删除用户失败，请检查网络连接');
      }
    };

    // 查询绑定信息
    const queryBindInfo = async (user) => {
      try {
        let apiUrl = '';
        let queryType = '';
        
        if (user.identity === 1) {
          // 老人查询绑定的家属信息
          apiUrl = `${API_BASE}/family/binding?id=${user.id}`;
          queryType = '绑定家属';
        } else if (user.identity === 2) {
          // 家属查询绑定的老人信息
          apiUrl = `${API_BASE}/family/bind`;
          queryType = '绑定老人';
        } else {
          console.warn('不支持的用户身份类型:', user.identity);
          alert('该用户类型不支持查询绑定信息');
          return;
        }


        const response = await fetch(apiUrl);
        const data = await response.json();

        if (data.code === 1) {
          // 设置绑定信息数据
          bindInfo.userName = user.name || '未知用户';
          bindInfo.queryType = queryType;
          bindInfo.data = data.data || [];
          

          showBindInfoModal.value = true;
        } else {
          console.error('查询绑定信息失败:', data.msg);
          alert(`查询${queryType}信息失败: ${data.msg}`);
        }
      } catch (error) {
        console.error('查询绑定信息失败:', error);
        alert('查询绑定信息失败，请检查网络连接');
      }
    };

    // 批量删除
    const openBulkDelete = async () => {
      if (selectedUsers.value.length === 0) {
        alert('请至少选择一个用户');
        return;
      }

      if (!confirm(`确定要删除选中的 ${selectedUsers.value.length} 个用户吗？`)) return;

      try {
        const ids = selectedUsers.value.join(',');
        const response = await fetch(`${API_BASE}/users?ids=${ids}`, {
          method: 'DELETE'
        });

        const data = await response.json();

        if (data.code === 1) {
          selectedUsers.value = [];
          if (showingAllUsers.value) {
            handleShowAllUsers();
          } else {
            handleSearch();
          }
          alert('批量删除成功');
        } else {
          alert(`批量删除失败: ${data.msg}`);
        }
      } catch (error) {
        console.error('批量删除失败:', error);
        alert('批量删除失败，请检查网络连接');
      }
    };

    // 打开添加模态框
    const openAddModal = () => {
      Object.assign(newUser, {
        name: '',
        gender: '1',
        idCard: '',
        phone: '',
        address: '',
        identity: '1',
        password: ''
      });
      showAddModal.value = true;
    };

    // 打开编辑模态框
    const openEditModal = (user) => {
      try {
        // 深拷贝用户数据，确保所有属性都被正确复制
        editingUser.value = {
          id: user.id,
          name: user.name || '',
          gender: user.gender || '1',
          idCard: user.idCard || '',
          phone: user.phone || '',
          emergency_phone: user.emergency_phone || '',
          address: user.address || '',
          identity: user.identity || '1',
          password: '' // 密码字段重置为空
        };
        
        // 确保模态框状态正确设置
        showEditModal.value = true;
      } catch (error) {
        console.error('打开编辑模态框时出错:', error);
        alert('打开编辑窗口失败，请重试');
      }
    };

    // 监听页码变化
    watch(currentPage, () => {
      if (showingAllUsers.value) {
        updateDisplayedUsers();
      } else {
        fetchUsers();
      }
    });

    // 返回管理员主界面
    const goBack = () => {
      router.push('/manager');
    };

    // 初始化加载数据
    onMounted(() => {
      handleShowAllUsers();
    });

    return {
      currentDate,
      displayedUsers,
      selectedUsers,
      selectAll,
      currentPage,
      pageSize,
      totalPages,
      searchParams,
      showAddModal,
      showEditModal,
      showBindInfoModal,
      bindInfo,
      newUser,
      editingUser,
      openAddModal,
      openEditModal,
      addUser,
      updateUser,
      deleteUser,
      queryBindInfo,
      openBulkDelete,
      handleSearch,
      handleShowAllUsers,
      goBack
    };
  }
}
